<template>
  <div class="category">
  		<h3 class="my-4">{{category.name}}{{ this.$route.params.id }}</h3>
        <div class="row">
            <div v-for="book in books" class="col-lg-4 col-md-6 mb-4">
              	<div class="card h-100">
	                <a href="#"><img class="card-img-top" src="http://placehold.it/700x400" alt=""></a>
	                <div class="card-body">
	                  	<h4 class="card-title">
	                    	<a href="#">Item One</a>
	                  	</h4>
	                  	<h5>$24.99</h5>
	                  	<p class="card-text">Lorem ipsum dolor sit amet, consectetur adipisicing elit. Amet numquam aspernatur!</p>
	                </div>
	                <div class="card-footer">
	                  	<small class="text-muted">&#9733; &#9733; &#9733; &#9733; &#9734;</small>
	                </div>
              	</div>
            </div>
        </div>
    </div>
</template>


<script>

export default {
  name: 'home',
  data () {
    return {
    	books: '',
    	category: '',
    	id: this.$route.params.id
    }
  },
  watch: {
  	id: function() {
  		console.log(this.$route.params.id)
  		this.axios.get(this.$parent.$parent.AJAX_URL + '/book/client/api/authors/' + this.$route.params.id).then((response) => {

          if (response.status == 200) {
            if (response.data.status) {
              this.category = response.data.data
            } else {
              console.log(response.data.message)
            }
          } else {
            console.log(response.data.message)
          }
    })
  	}
  },
  created() {
  	  	this.axios.get(this.$parent.$parent.AJAX_URL + '/book/client/api/authors/' + this.$route.params.id).then((response) => {

          if (response.status == 200) {
            if (response.data.status) {
              this.category = response.data.data
            } else {
              console.log(response.data.message)
            }
          } else {
            console.log(response.data.message)
          }
    })
  }
}
</script>


<style>

</style>



